export default function LoadingSpinner() {
  return (
    <div className="fixed inset-0 flex items-center justify-center bg-black/30 backdrop-blur-sm z-50">
      <div className="relative">
        
        {/* Glow effect behind the earth */}
        <div className="absolute inset-0 blur-2xl bg-blue-500/30 animate-pulse"></div>

        {/* Earth orb */}
        <div className="w-24 h-24 rounded-full animate-spin-slow relative overflow-hidden shadow-[0_0_30px_rgba(0,150,255,0.7)]">

          {/* Earth gradient */}
          <div className="absolute inset-0 bg-[radial-gradient(circle_at_30%_30%,#4fc3f7,#01579b)]" />

          {/* Continents shadow (gives 3D depth) */}
          <div className="absolute inset-0 bg-[radial-gradient(circle_at_60%_40%,rgba(0,0,0,0.3),rgba(0,0,0,0))]" />

          {/* Atmospheric glow */}
          <div className="absolute inset-0 rounded-full border border-blue-300/20 shadow-[inset_0_0_20px_rgba(255,255,255,0.4)]" />
        
        </div>
      </div>
    </div>
  );
}